import React from "react";
import { createRoot } from "react-dom/client";
import { Button, Text, Flex, Box, Container, Card } from "@radix-ui/themes";
import Providers from "@/shared/Providers";

function OptionsApp() {
  const [status, setStatus] = React.useState<{
    message: string;
    type: "success" | "error" | null;
  }>({
    message: "",
    type: null,
  });

  function requestMicrophonePermission() {
    navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then(function (stream) {
        setStatus({
          message: "Microphone permission granted!",
          type: "success",
        });
        // Remember to stop the stream after getting permission
        stream.getTracks().forEach((track) => track.stop());
      })
      .catch(function (err) {
        setStatus({
          message: `Error: ${err.message}`,
          type: "error",
        });
      });
  }

  return (
    <Box style={{ minHeight: "100vh" }} p="6">
      <Container size="1">
        <Card size="2">
          <Flex direction="column" gap="5">
            <Text size="8" weight="bold" as="div">
              Extension Options
            </Text>

            <Text as="p" size="3" color="gray">
              Click the button below to request microphone permissions:
            </Text>

            <Flex direction="column" gap="3">
              <Button
                size="3"
                variant="soft"
                onClick={requestMicrophonePermission}
              >
                Request Microphone Permission
              </Button>

              {status.message && (
                <Text
                  size="2"
                  color={status.type === "success" ? "green" : "red"}
                  weight="medium"
                >
                  {status.message}
                </Text>
              )}
            </Flex>
          </Flex>
        </Card>
      </Container>
    </Box>
  );
}

// Wait for DOM content to load before rendering
document.addEventListener("DOMContentLoaded", function () {
  const container = document.getElementById("root");
  if (container) {
    const root = createRoot(container);
    root.render(
      <Providers>
        <OptionsApp />
      </Providers>
    );
  }
});
